<template>
  <div>
    <div class="border rounded" style="height:80px">
      <div>
        <!-- <div class="font-weight-bold pt-3">Средняя высота</div> -->
        <div class="pt-3">Средняя высота</div>
        <div v-bind:class="classSpinner">
          <b-spinner label="Loading..."></b-spinner>
        </div>
        <div v-bind:class="classCnt" style="font-size:20px; color:red">{{ averLat }} м</div>
      </div>
    </div>
  </div>
</template>

<script>
import db from "./firebase-init.js";
import { todayDates, yesterdayDates } from "../getDates";

export default {
  name: "averageLatitude",
  props: {
    type: String
  },
  data() {
    return {
      averLat: 0,
      classSpinner: "",
      classCnt: "d-none"
    };
  },
  created: function() {
    if (this.type == "today") {
      let date = todayDates.pathDateFull;
      let flightsObj = db.ref(date);

      flightsObj.once("value").then(Snapshot => {
        const res = Snapshot.val();
        this.averLat =
          Math.round(
            Object.values(res).reduce(
              (acc, c) => acc + (c.geo_altitude || 0),
              0
            ) / Object.values(res).length
          ) - 325;
      });
    }

    if (this.type == "yesterday") {
      let date = yesterdayDates.pathDateFull;
      let flightsObj = db.ref(date);

      flightsObj.once("value").then(Snapshot => {
        const res = Snapshot.val();
        this.averLat =
          Math.round(
            Object.values(res).reduce(
              (acc, c) => acc + (c.geo_altitude || 0),
              0
            ) / Object.values(res).length
          ) - 325;
      });
    }

    if (this.type == "month") {
      let date = todayDates.pathMonth;
      let flightsObj = db.ref(date);

      flightsObj.once("value").then(Snapshot => {
        const res = Snapshot.val();

        let sum = 0;
        let average = Object.values(res).length;

        Object.values(res).forEach(function(item) {
          sum =
            sum +
            Math.round(
              Object.values(item).reduce(
                (acc, c) => acc + (c.geo_altitude || 0),
                0
              ) / Object.values(item).length
            );
        });
        this.averLat = Math.round(sum / average) - 325;
      });
    }

    if (this.type == "year") {
      let date = todayDates.pathYear;

      let flightsObj = db.ref(date);

      flightsObj.once("value").then(Snapshot => {
        const res = Snapshot.val();

        let sum = 0;
        let average = 0;

        Object.values(res).forEach(function(item) {
          average = average + Object.values(item).length;
          Object.values(item).forEach(function(resItem) {
            sum =
              sum +
              Math.round(
                Object.values(resItem).reduce(
                  (acc, c) => acc + (c.geo_altitude || 0),
                  0
                ) / Object.values(resItem).length
              );
          });
        });

        this.averLat = Math.round(sum / average) - 325;
      });
    }
  },
  beforeUpdate() {
    this.classSpinner = "d-none";
    this.classCnt = "";
  }
};
</script>

