/* eslint-disable no-console */

let today = new Date();
today.toLocaleDateString('ru-RU')

let yesterday = new Date();
yesterday.setDate(yesterday.getDate() - 1);
yesterday.toLocaleDateString();

const monthNamesDates = ["января", "февраля", "марта", "апреля", "мая", "июня",
  "июля", "августа", "сентября", "октября", "ноября", "декабря"
];

const monthNames = ["Январь", "Февраль", "Март", "Апрель", "Май", "Июнь",
  "Июль", "Август", "Сентябрь", "Октябрь", "Ноябрь", "Декабрь"
];

function getDates(date) {
    return {
        pathDateFull: date.getFullYear() + '/' + ("0" + (date.getMonth() + 1)).slice(-2) + '/' + ("0" + date.getDate()).slice(-2) + '-' +  ("0" + (date.getMonth() + 1)).slice(-2) + '-' + date.getFullYear(),
        pathMonth: date.getFullYear()  + '/' + ("0" + (date.getMonth() + 1)).slice(-2),
        pathYear: date.getFullYear(),
        day: date.getDate(),
        monthNameDates: monthNamesDates[date.getMonth()],
        monthName: monthNames[date.getMonth()],
        year: date.getFullYear()
    }
}

let todayDates = getDates(today);
let yesterdayDates = getDates(yesterday);

export {
    todayDates,
    yesterdayDates
};




