<template>
  <div class="border rounded" style="height:80px">
    <div>Количество самолетов</div>
    <div>
      <b-spinner v-bind:class="classSpinner" label="Loading..."></b-spinner>
    </div>
    <div v-bind:class="classCnt" style="font-size:40px; color:red">
      {{ cnt }}
      <span style="font-size:20px; color:black">{{ typeCount }}</span>
    </div>
  </div>
</template>

<script>
import db from "./firebase-init.js";
import { todayDates, yesterdayDates } from "../getDates";

export default {
  name: "aircraftCount",
  props: {
    type: String,
    typeCount: String
  },
  data() {
    return {
      cnt: 0,
      classSpinner: "",
      classCnt: "d-none"
    };
  },
  created: function() {
    if (this.type == "today") {
      let date = todayDates.pathDateFull;
      let flightsObj = db.ref(date);

      flightsObj.once("value").then(Snapshot => {
        const res = Snapshot.val();
        this.cnt = Object.values(res).length;
      });
    }

    if (this.type == "yesterday") {
      let date = yesterdayDates.pathDateFull;
      let flightsObj = db.ref(date);

      flightsObj.once("value").then(Snapshot => {
        const res = Snapshot.val();
        this.cnt = Object.values(res).length;
      });
    }

    if (this.type == "month") {
      let date = todayDates.pathMonth;
      let flightsObj = db.ref(date);

      flightsObj.once("value").then(Snapshot => {
        const res = Snapshot.val();
        let sum = 0;
        Object.values(res).forEach(function(item) {
          sum = sum + Object.values(item).length;
        });
        this.cnt = sum;
      });
    }

    if (this.type == "year") {
      let date = todayDates.pathYear;
      let flightsObj = db.ref(date);

      flightsObj.once("value").then(Snapshot => {
        const res = Snapshot.val();
        let sum = 0;
        Object.values(res).forEach(function(item) {
          Object.values(item).forEach(function(resItem) {
            sum = sum + Object.values(resItem).length;
          });
        });
        this.cnt = sum;
      });
    }
  },
  beforeUpdate() {
    this.classSpinner = "d-none";
    this.classCnt = "";
  }
};
</script>

